.textarea {
  width: 100%;
  height: 200px;
  resize: vertical;
}

.keyboard {
  border: 1px solid #000;
  border-radius: 3px;
}

.keyboard__layout {
  padding: 5px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.keyboard__row {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  gap: 5px;
}

.key {
  user-select: none;
  content: "K";
}
.key_Space {
  flex-grow: 1;
}

.wrapper {
  width: 800px;
  margin: auto;
}